import * as React from "react";
import Dialog from "@reach/dialog";
import type { ActionFunction, LinksFunction, LoaderFunction } from "remix";
import { useLoaderData } from "remix";
import { json } from "remix";
import { useParams } from "remix";
import { Form } from "remix";
import { useNavigate } from "remix";
import styles from "@reach/dialog/styles.css";

import stylesUrl from "~/styles/invoices/edit.css";

export let links: LinksFunction = () => {
  return [
    {
      rel: "stylesheet",
      href: styles
    },
    {
      rel: "stylesheet",
      href: stylesUrl
    }
  ];
};

export let loader: LoaderFunction = ({ params }) => {
  return json({
    id: params.id,
    company: "Remix",
    description: "Invoice for Remix license",
    amount: "200",
    date: "10/10/2021"
  });
};

export let action: ActionFunction = ({ request }) => {
  console.log(request);
  return null;
};

export default function Edit() {
  const navigate = useNavigate();
  const { id } = useParams();
  const data = useLoaderData();

  const [amount, setAmount] = React.useState(data.amount);
  const [description, setDescription] = React.useState(data.description);
  const [company, setCompany] = React.useState(data.company);
  const [date, setDate] = React.useState(data.date);

  function onDismiss() {
    navigate(-1);
  }
  return (
    <Dialog
      className="dialog"
      isOpen={true}
      aria-label="Add invoice"
      onDismiss={onDismiss}
    >
      <Form method="post" replace>
        <label className="label">Company</label>
        <input className="input" type="text" name="company" value={company} />

        <label className="label">Description</label>
        <input
          className="input"
          type="text"
          name="description"
          value={description}
          rows={5}
        />

        <label className="label">Amount</label>
        <input className="input" type="number" name="amount" value={amount} />

        <div>
          <button type="submit">Save</button>
          <button type="button" onClick={onDismiss}>
            Cancel
          </button>
        </div>
      </Form>
    </Dialog>
  );
}
